.button-black {
    background: black;
    color: white;
    padding: 8px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    align-items: center;
  }
  
  .button-black .ceta {
    margin-left: 5px;
  }
  .button-gray{
    border: none;
    padding: 5px 10px;
    font-size: 12px;
    border-radius: 5px;
    background-color: rgb(221, 221, 221);
  }